import React, { useState, useEffect } from "react";
import { Map, TileLayer, Marker, Popup } from 'react-leaflet'
import './styles/location.scss'

type Position = { lat: number, lng: number }

const LocationPickerComponent = ({onPick} : {onPick? : (loc : Position) => void}) => {
  const [location, setLocation] = useState<[number, number]>([52.672869, 12.988025])

  function updatePosition(args : any) {
    console.log(args)
  }

  return(
    <div className='location_picker'>
      <Map center={location} zoom={10}>
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        <Marker
          draggable={false}
          onDragend={updatePosition}
          position={location}>
        </Marker>
      </Map>
    </div>
  )
}

export { LocationPickerComponent }